import axios from 'axios';
import store from '../store/store';


export class Api {
  private static HOST = 'https://mkn.ram.m2m.telekom.com';
  private static LOGIN_URL = 'user/currentUser';


  static login(authToken: string) {
    return axios.get(`${this.HOST}/${this.LOGIN_URL}`, {headers: {Authorization: `Basic ${authToken}`}});
  }

  static get(url: string) {
    return axios.get(`${this.HOST}/${url}`, {headers: {Authorization: `Basic ${store.getters.AUTH_TOKEN}`}});
  }


}


