<template>
    <div>
      <div class="columns" >
        <div class="column is-one-quarter box"
             style="background-color: #D8F1C9; position: absolute;right: 10%;bottom: 10%;">
          <ErrorMessage v-if="errorMessage" style="margin-top: 15px;">{{errorMessage}}</ErrorMessage>
          <div class="field" style="margin-top: 18px;">
            <div class="control">
              <input
                class="input"
                type="email"
                style="border-color: #70AD47"
                placeholder="Benutzername"
                v-model="user.email">
            </div>
          </div>

          <div class="field">
            <div class="control">
              <input
                class="input"
                type="password"
                style="border-color: #70AD47"
                placeholder="Passwort"
                v-model="user.password">
            </div>
          </div>
          <a class="button" style="margin-bottom: 18px; background-color: #A9D18E; border-color: #70AD47" @click="login">Anmelden</a>
        </div>

      </div>

      <img src="../assets/rewe_to_go_back.jpg" style="height: 99vh; width: 100%">
    </div>
</template>

<script>

import Vue from 'vue';


export default Vue.extend({
  data() {
    return {
      user : {
        email: '',
        password: ''
      },
      errorMessage: ''
    };
  },
  methods: {
   login() {
     this.$store.dispatch('LOGIN', this.user).then(
       res => {
         this.$store.commit('SET_USER', res.data);
         const token = window.btoa(`${this.user.email}:${this.user.password}`);
         this.$store.commit('SET_AUTH', token);
         this.$router.push({name: 'cooking'});
       }
     ).catch(err => {
        this.errorMessage = 'Invalid email or password';
     });
   }
 }

});
</script>

