import {Module} from 'vuex';
import {Api} from '@/utils/api';

const AUTH_KEY = 'auth';

interface IAuthState {
  email: string;
  username: string;
  authToken: string | null;
}

const authState: Module<IAuthState, {}> = {
  state: {
    email: '',
    username: '',
    authToken: ''
  },
  actions: {
    LOGIN({commit}, user) {
      const authToken = window.btoa(`${user.email}:${user.password}`);
      return Api.login(authToken);
    }
  },
  mutations: {
    SET_USER(state, user) {
      state.email = user.email;
      state.username = `${user.firstName} ${user.lastName}`;
    },
    SET_AUTH(state, token) {
      state.authToken = token;
      if(token){
        localStorage.setItem(AUTH_KEY, token);
      }else {
        localStorage.removeItem(AUTH_KEY);
      }
    }
  },
  getters: {
    AUTH_TOKEN : state => {
      if(!Boolean(state.authToken)) {
        state.authToken = localStorage.getItem(AUTH_KEY)
      }
      return state.authToken
    },
    IS_AUTHORIZED : state => Boolean(state.authToken)
  }
};


export default authState;
